import "./App.css";
import Pho1 from "./assets/imgs/Photo1.png";
import Pho2 from "./assets/imgs/Photo2.png";
import Pho3 from "./assets/imgs/Photo3.png";
import Pho4 from "./assets/imgs/Photo4.png";
import Pho5 from "./assets/imgs/Photo5.png";
import Pho6 from "./assets/imgs/Photo6.png";
import Pho7 from "./assets/imgs/Photo7.png";
import Pho8 from "./assets/imgs/Photo8.png";
import Pho9 from "./assets/imgs/Photo9.png";
import Pho10 from "./assets/imgs/Photo10.png";
import hb from "./assets/audio/hb.mp3";
import { Component, useEffect, useState } from "react";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      phos: [Pho1, Pho2, Pho3, Pho4, Pho5, Pho6, Pho7, Pho8, Pho9, Pho10],
      CP: 0,
    };
  }

  componentDidMount() {
    let i = 0;
    setInterval(() => {
      i >= this.state.phos.length - 1 ? (i = 0) : i++;
      this.setState({ CP: i });
    }, 3400);
  }

  render() {
    return (
      <div className="container-fluid MAIN">
        <audio src={hb} loop></audio>
        <div className="jumbotron">
          <h3 className="text-center TAG">Happy Birthday</h3>
          <h3 className="text-center TAG">My Love...My Life</h3>
        </div>
        <div className="photo-box">
          <img
            className="img-rounded ANI"
            src={this.state.phos[this.state.CP]}
          />
        </div>
        <div className="jumbotron MESS">
          <h4 className="text-center">
            Wish you a very happy birthday, May life lead you to great happiness
            success and hope that all your wishes comes true. Enjoy your day!
            Happy Birthday....MyLove....
            <br></br>
            🎂🎉🎊💐😘😘😘❤️🥰
          </h4>
        </div>
      </div>
    );
  }
}

export default App;
